import React, { Component } from 'react';
import Icon from '../../../../public/Icon/Icon';
import { NavLink, Router, Switch, Route } from 'react-router-dom';
import KnowledgeMember from '../KnowledgeMember/KnowledgeMember';
import KnowledgeAnalysis from '../KnowledgeAnalysis/KnowledgeAnalysis';
import KnowledgeRecycle from '../KnowledgeRecycle/KnowledgeRecycle';
import KnowledgeSetting from '../KnowledgeSetting/KnowledgeSetting';
import { createHashHistory } from 'history';
import './index.css';

const history = createHashHistory();

export default class KnowledgeManager extends Component {
  constructor(props) {
    super(props);
    const { pathname } = props.location;
    if (pathname === '/knowledge/manager') {
      props.history.replace('/knowledge/manager/member')
    }

    this.state = {
      knowledgeName: '交互毕业设计组',
      url: '/knowledge/manager',
    }
  }

  render() {
    const { knowledgeName, url } = this.state;
    return (
      <div className="knowledgeManager">
        <div className="title-bar-public flex noJustify">
          <h2 onClick={() => {
            this.props.history.go(-1);
          }} className="title flex curser-pointer">
            <Icon className="defaultIcon" type="iconzu190" />
            {
              knowledgeName
            }
          </h2>
          <div className="nav">
            <NavLink className="group-menu-item" activeClassName="active" to={`member`}>成员</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`analysis`}>统计</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`recycle`}>回收站</NavLink>
            <NavLink className="group-menu-item" activeClassName="active" to={`setting`}>设置</NavLink>
          </div>
        </div>
        <div className="main-content">
          <Router history={history}>
            <Switch>
              <Route exact path={`${url}/member`} component={() => <KnowledgeMember />} />
              <Route exact path={`${url}/analysis`} component={() => <KnowledgeAnalysis />} />
              <Route exact path={`${url}/recycle`} component={() => <KnowledgeRecycle />} />
              <Route exact path={`${url}/setting`} component={() => <KnowledgeSetting />} />
            </Switch>
          </Router>
        </div>
      </div>
    )
  }
}