import React, { Component } from 'react';
import { Button } from 'antd';
import { createFromIconfontCN } from '@ant-design/icons';
import KnowledgeItem from './KnowledgeItem/KnowledgeItem';

import './index.css';

const IconFont = createFromIconfontCN({
  scriptUrl: '//at.alicdn.com/t/font_1755333_zinaf1xwqgh.js',
});

export default class GroupMainContent extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      knowledgeList: [],
      isShowGuide: true,
    }
  }

  componentDidMount() {
    const { knowledgeList } = this.state;
    for (let i = 0; i < 5; i++) {
      knowledgeList.push({
        id: `2ajhsduijah${i}`,
        knowledgeName: '项目方案',
        desc: '跟进项目进度，存储项目文档',
        updatedDocs: [
          {
            id: `3jhjks${i}dsd`,
            title: '产品研发进度表',
            updatedTime: '今天 14:24',
          },
          {
            id: `3jhjks${i}dsd33`,
            title: '产品研发计划',
            updatedTime: '今天 14:24',
          },
          {
            id: `wjhajhd${i}`,
            title: '项目运营方案',
            updatedTime: '今天 14:24',
          }
        ]
      })
    }

    this.setState({
      knowledgeList,
    })
  }

  render() {
    const { knowledgeList, isShowGuide } = this.state;
    return (
      <div className="main-section">
        {isShowGuide && <div className="guide-card">
          <IconFont onClick={() => {this.setState({isShowGuide: false})}} className="closeBtn" type="iconzujian51" />
          <img style={{height: '175px', marginRight: '21px'}} src={require('../../../../assets/images/group_guide01.png')} alt=""/>
          <div className="guide-text">
            <h3>欢迎来到鲸落团队</h3>
            <p className="desc-paragraph">
              点击下方添加成员或新建知识库，与团队成员一起创作与分享知识。
            </p>
            <div className="actionBtns">
              <Button type="primary">添加成员</Button>
              <Button className="defaultBtn">新建知识库</Button>
            </div>
          </div>
        </div>}
        <div className="knowledge-list-cards">
          {
            knowledgeList.map(item => 
              <KnowledgeItem key={item.id} {...item} />
            )
          }
        </div>
      </div>
    )
  }
}