import React, { Component } from 'react';
import SharedTable from '../../../public/SharedTable';
import SearchBar from '../../../public/SearchBar';
import Icon from '../../../public/Icon/Icon';

import './index.css';

export default class Group extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '团队名称',
        key: 'groupName',
        width: 300
      },
      {
        title: '简介',
        key: 'groupDesc',
        width: 360
      },
      {
        title: '团队人数',
        key: 'groupCounts',
        width: 100,
      }
    ];

    this.state = {
      columns,
      groupList: [],
    }

  }

  componentDidMount() {
    const { groupList } = this.state;
    for (let i = 0; i < 8; i++) {
      groupList.push({
        id: `a23jh28${i}w2`,
        groupName: '交通工具设计菜狗大队',
        groupDesc: '为了完成交通工具设计',
        groupCounts: 4 + '人',
        icon: () => <Icon className="middle-icon" type="iconzu22" />,
      })
    };

    this.setState({
      groupList,
    })
  }

  onSearch = keyWords => {
    console.log(keyWords, '请求搜索接口数据');
  }

  render() {
    const { columns, groupList } = this.state;
    return <div className="group-list">
      <SearchBar 
        onClick={() => {window.location.href="#/group/createNew"}} 
        onSearch={values => this.onSearch(values)} 
        searchTitle="团队" 
        searchBtnText="新建团队" 
      />
      <SharedTable parentPath="#/group/detail/" columns={columns} dataSource={groupList} />
    </div>
  }
}