export const getUsersInfo = () => {
  const baseId = `31161040`;
  const AName = '解兴田-王阿敏-赵璞-焦一冰-刘芮畅-赵心雨-李丹培-甘林平-王盼-何庆湘-程若琦-朱静-谢毅鹏-王傲飞-孙铭泽-姬惠凯-罗技纹-张世恒-柳洋-丁义秋-项德元-赵柯柯-武文亮-高黎明-丘剑涛-侯宇-吴豪-吕精珂-魏家强-牛东琦-闵杰';
  const BName = '潘晶晶-王淇-王杰-张凯-杨柳-杨颖-耿文尧-张思迪-李桐-马俊杰-闫富华-张浩铭-蔺长浩-李幸运-贾亚坤-李培博-旷晗-张跃-刘铭-张欢-宋原江-梁广奇-贾成林-闫壮壮-张壮壮-雷金峰-崔宁-甘新宇-万晨炜';
  const AStudent = AName.split('-').map((stu, index) => {
    const number = index + 1;
    return {
      id: `${baseId}1${number > 10 ? number : `0${number}`}`,
      name: stu,
      sex: index < 12 ? '女' : '男',
      className: '工设1601',
      group: 16,
      isManager: stu === '刘芮畅',
    }
  });

  const BStudent = BName.split('-').map((stu, index) => {
    const number = index + 1;
    return {
      id: `${baseId}1${number > 10 ? number : `0${number}`}`,
      name: stu,
      sex: index < 12 ? '女' : '男',
      className: '工设1602',
      group: 16,
      isManager: stu === '旷晗',
    }
  })

  return {
    AStudent,
    BStudent,
    allStudent: [...AStudent, ...BStudent],
  }
};

export const getGroupInfo = (id, keyword, courseId, studentCode) => {
  const { allStudent } = getUsersInfo();
  const groupName = '创新队-冠亚队-增值队-飞鹰队-超凡队-瑞旗队-狼性队-蚂蚁队-大雁团队-冲锋队-九段队-速度队-改变队-越众队-责任队';
  const groupList = groupName.split('-').map((item, index) => {
    const randomNum = parseInt((Math.random() + 1) * 100000);
    const obj = {
      id: `group_${randomNum}${index}`,
      name: item,
      member: [],
      courseName: '交互设计',
      desc: '一切为了团队，多多沉淀知识',
      avatar: null,
    };

    const randNum = parseInt(Math.random() * 5);

    for (let i = 0; i < randNum; i++) {
      obj.member.push(allStudent[parseInt(Math.random() * allStudent.length)]);
    }

    return obj;
  });

  return groupList; 
}