import React, { Component } from 'react';
import MainGuidePage from '../../MainGuidePage';
import SelectMenu from '../../../public/SelectMenu';
import ActiveItem from './ActiveItemCards/ActiveItem';
import { Button } from 'antd';
import './index.css';

const options = [
  {
    label: '文档',
    value: 'docs',
  },
  {
    label: '知识库',
    value: 'knowledge',
  },
  {
    label: '团队',
    value: 'team',
  }
]

export default class Main extends Component {
  state = {
    isNew: true,
    newTemplateList: [],
    hasNoData: false,
    activeList: [],
  }

  async componentDidMount() {
    const { newTemplateList, activeList } = this.state;
    const titleArr = ['交互设计', '视觉传达设计', '材料工艺设计', '项目组', '文档知识库', '画板知识库'];
    const descArr = ['规范设计流程，激发设计灵感。', '规范设计流程，优化团队协作。', '规范设计流程，优化团队协作。', '沉淀项目文档，在线异步协同。', '创建在线文档表格。', '上传并预览设计稿。']
    for (let i = 0; i< 6; i++) {
      newTemplateList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: titleArr[i],
        desc: descArr[i],
      });
      activeList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: '关于消防车种类及其功用调研的访谈记录',
        courseName: '装备设计课',
        groupName: '挖掘机大队',
        knowledgeName: '装备兴趣小组',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '高黎明',
        behavior: 'update',
        content: '文档'
      });
    }

    this.setState({
      newTemplateList,
      activeList,
    })
  }

  SelectItems = values => {
    if (values.length === 0) {
      this.setState({
        hasNoData: true,
      })
    } else {
      this.setState({
        hasNoData: false,
      })
      console.log('请求接口，获取数据');
    }
  }

  onEdit = item => {
    console.log(item, '编辑窗口')
  }

  getMoreData = () => {
    const { activeList } = this.state;
    for (let i = 0; i < 6; i++) {
      activeList.push({
        id: `${i}8000${i + 1}${i + 4}`,
        title: '关于消防车种类及其功用调研的访谈记录',
        courseName: '装备设计课',
        groupName: '挖掘机大队',
        knowledgeName: '装备兴趣小组',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '高黎明',
        behavior: 'update',
        content: '文档'
      });
    };
    this.setState({
      activeList,
    })
  }

  render() {
    const { isNew, newTemplateList, hasNoData, activeList } = this.state;

    return <>
      {
        isNew && newTemplateList.length > 0 && <MainGuidePage list={newTemplateList} />
      }
      <div className="dashboard-main-page">
        <div className="latest-new">
          <SelectMenu
            options={options} 
            onChange={(values) => this.SelectItems(values)}
          />
          {
            hasNoData && (
              <div>没有数据</div>
            )
          }
          {
            !hasNoData && <>
              {activeList.map(item => <ActiveItem key={item.id} onEdit={() => this.onEdit(item)} activeItem={item}></ActiveItem>)}
              <Button 
                onClick={() => this.getMoreData()}
                style={
                  {
                    width: '100%', 
                    lineHeight: '40px', 
                    height: '60px',
                    border: 0,
                    boxShadow: '0px 3px 14px rgba(0,0,0,0.04)'
                  }
                }
              >加载更多</Button>
            </>
          }
        </div>
        <div className="right-area">
          <div className="today-support">
            今日推荐模块
            <div className="support-image">
              <div className="actions-bar">
                <span>知识库标题</span>
                <span>知识库内文档标题</span>
              </div>
            </div>
          </div>
          <div className="quickly-entry">
            快捷入口
            <ul className="entries">
              <li>交互设计--前期调研</li>
              <li><a>添加</a></li>
            </ul>
          </div>
        </div>
      </div>
    </>
  }
}