import axios from 'axios';
import { Modal } from 'antd';
import DialogModal from '../public/DialogModal/DialogModal'

// 后端请求接口主地址
const BASE_URL = 'http://www.jingluo.ink';
export const request = (method, api, params, needToken, needAlert) => {
  const token = localStorage.getItem('token');
  if (needToken && !token) {
    // 测试时关闭
    // DialogModal.show({
    //   title: '温馨提示',
    //   type: 'warning',
    //   content: '为了您更好的使用体验，请先登录鲸落',
    //   isOnlyOk: true,
    //   onOk: () => {
    //     window.location.href="#/login"
    //   }
    // });
    return ;
  }  else {
    switch (method) {
      case 'post':
        return axios.post(`${BASE_URL}${api}?token=${token}`, {...params, token});
        break;
      case 'get':
        return axios.get(`${BASE_URL}${api}`, {...params, token});
        break;
      default:
        break;
    }
  }
}
export const POST = (api, params, needToken = true, needAlert = false) => {
  return request('post', api, params, needToken, needAlert);
}

export const GET = (api, params, needToken, needAlert) => 
  axios.get('get', `${BASE_URL}${api}`, params, needToken, needAlert);