import React, { Component } from 'react';
import { Radio, Button, Form, Input, Select, Avatar, Upload } from 'antd';
import Icon from '../../../../public/Icon/Icon';
import { connect, actions } from 'mirrorx';
import '../index.css';

const { Option } = Select;

class KnowledgeCreateNew extends Component {
  constructor(props) {
    super(props);
    
    const userInfo = {
      name: '一叶知秋',
      studentCode: '311617040124',
    }

    this.state = {
      userInfo,
      currentStep: 1,
      templateType: 'Auto',
      knowledgeTypeOptions: [
        {
          title: '文档知识库',
          desc: '创建在线文档表格',
          id: 'docs',
          isSelected: true,
        },
        {
          title: '画板知识库',
          desc: '存储、分享你的作品',
          id: 'dashboard',
          isSelected: false,
        }
      ],
      templateOptions: [],
      currentTag: 'person',
      selectedTemplate: undefined,
      knowledgeList: [],
      selectedId: undefined,
      selectedType: 'docs',
      belongs: userInfo.name,
    };
  }

  async componentDidMount() {
    const { knowledgeTypeOptions, templateOptions, currentTag } = this.state;
    const knowledgeList = await actions.knowledge.getKnowledgeList();
    const templateName = ['知识专栏', '学习笔记', '交互设计', '选择已有模板'];
    const templateDesc = ['归纳与分享知识', '分享与交流你的知识', '分享与交流你的知识', '导入曾用的知识库模板']

    for (let i = 0; i < 4; i++) {
      templateOptions.push({
        id: `template_class_${i}`,
        templateName: templateName[i],
        templateDesc: `${templateDesc[i]}。`,
        isSelected: false,
      })
    };

    templateOptions[0].isSelected = true;

    this.setState({
      knowledgeTypeOptions,
      templateOptions,
      selectedTemplate: currentTag === 'class' ? 
        templateOptions[0].id : 
        knowledgeTypeOptions[0].id,
      knowledgeList,
      selectedIds: knowledgeList[0] && [knowledgeList[0].id],
    });
  }

  onStepChange = (step) => {
    this.setState({
      currentStep: step,
    });
  }


  onSelectTag = (e) => {
    const { templateOptions, knowledgeTypeOptions, userInfo } = this.state;

    this.setState({
      currentTag: e.target.value,
      selectedTemplate: e.target.value === 'class' ? templateOptions[0].id : knowledgeTypeOptions[0].id,
      belongs: e.target.value === 'class' ? templateOptions[0].templateName : userInfo.name,
    });
  }

  onSelectTemplate = e => {
    this.setState({
      selectedTemplate: e.target.value,
    })
  }

  renderLeftSection = () => {
    const { 
      currentStep, 
      knowledgeTypeOptions, 
      templateOptions, 
      currentTag, 
      selectedTemplate,
      knowledgeList,
      selectedIds,
      selectedType,
      belongs,
    } = this.state;
    
    if (currentStep == 0) {
      return (
        <>
          <div className="tagSection section">
            <h3>标签</h3>
            <Radio.Group 
              onChange={this.onSelectTag} 
              className="tagSelector" 
              value={currentTag} 
              buttonStyle="solid"
            >
              <Radio.Button key="person" value="person">
                个人
              </Radio.Button>
              <Radio.Button key="course" value="course">
                团队
              </Radio.Button>
            </Radio.Group>
          </div>
          {
            currentTag === 'class' && <div className="belongSection section">
              <h3>归属</h3>
              <Select 
                className="selector"
                value={selectedIds}
                size="large"
                onChange={
                  (selectedIds) => {
                    this.setState({
                      selectedIds,
                      belongs: knowledgeList.filter(item => item.id === selectedIds[0])[0].knowledgeName,
                    })
                  }
                }
              >
                {
                  knowledgeList.map(item => (
                    <Option
                      key={item.id}
                      className="option-knowledge"
                    >
                      {!item.icon && <Icon className="side-icon-item" type="iconzu307" />}
                      <span>{item.knowledgeName}</span>
                    </Option>
                  ))
                }
              </Select>
            </div>
          }
          <div className="templateSection section">
            <h3>知识库类型</h3>
            <Radio.Group 
              onChange={this.onSelectTemplate} 
              className="templateSelector knowledge-type-selector flex" 
              buttonStyle="solid" 
              value={selectedType}
            >
              {
                knowledgeTypeOptions.map((type, index) => (
                  <Radio.Button value={type.id}>
                    <h3 style={{color: type.id === selectedType && '#fff'}}>
                      {type.title}
                    </h3>
                    <p style={{color: type.id === selectedType && '#fff'}} className="desc-paragraph">
                      {type.desc}
                    </p>
                  </Radio.Button>
                ))
              }
            </Radio.Group>
          </div>
          <div className="templateSection section">
            <h3>从模板新建</h3>
            <Radio.Group 
              onChange={this.onSelectTemplate} 
              className="templateSelector knowledge-type-selector flex" 
              buttonStyle="solid" 
              value={selectedTemplate}
            >
              {
                templateOptions.map(template => (
                  <Radio.Button value={template.id}>
                    <h3 style={{color: template.id === selectedTemplate && '#fff'}}>
                      {template.templateName}
                    </h3>
                    <p style={{color: template.id === selectedTemplate && '#fff'}} className="desc-paragraph">
                      {template.templateDesc}
                    </p>
                  </Radio.Button>
                ))
              }
            </Radio.Group>
          </div>
          <Button type="primary" onClick={() => this.onStepChange(1)} className="actionButton">
            下一步
          </Button>
        </>
      )
    } else if (currentStep == 1) {
      return (
        <>
          <div className="goBack">
            <Icon onClick={() => this.onStepChange(0)} type="iconlujing36" />
            <span className="tipText">返回重新选择</span>
          </div>
          <Form
            className="groupForm"
            layout="vertical"
            onFinish={(values) => {
              console.log('22', values);
              this.props.history.push('/knowledge/detail/hashdqw');
            }}
            initialValues={
              {
                knowledgeName: undefined,
                knowledgeDesc: undefined,
                belongs,
              }
            }
          >
            <Form.Item
              label="名称"
              name="knowledgeName"
              rules={[{ required: true, message: '请输入知识库名称' }]}
            >
              <Input placeholder="例：一号知识库" />
            </Form.Item>
            <Form.Item
              label="简介"
              name="knowledgeDesc"
            >
              <Input.TextArea placeholder="例：交互设计知识总结一期" />
            </Form.Item>
            <Form.Item
              label="归属"
              name="belongs"
            >
              <div className="belongTag">
                {
                  currentTag === 'person' ? `一叶之秋（个人）` : 
                    `${knowledgeList.filter(item => item.id === selectedIds[0])[0].knowledgeName}（团队）`
                }
              </div>
            </Form.Item>
            <Form.Item
              label="可见范围"
              name="visibleRange"
            >
              <Select className="visibleRangeSelector" defaultValue="team">
                <Option value="team">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />仅团队成员可见
                  </h3>
                  <p className="desc-paragraph">
                    知识库仅团队成员可见
                  </p>
                </Option>
                <Option value="public">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />
                    互联网可见
                  </h3>
                  <p className="desc-paragraph">
                    向社区公开团队知识库
                  </p>
                </Option>
              </Select>
            </Form.Item>
            <Form.Item
              label="头像"
              name="avatar"
            >
              <div className="flex noJustify">
                <Avatar size={64} />
                <Upload className="uploadBox">
                  <Button className="defaultBtn">
                    上传头像
                  </Button>
                  <p className="noMargin desc-paragraph">点击上传按钮或头像上传</p>
                </Upload>
              </div>
            </Form.Item>
            <Form.Item>
              <Button className="primaryBtn" block={true} htmlType="submit" type="primary">创建</Button>
            </Form.Item>
          </Form>
        </>
      )
    }
  }

  renderRightSection = () => {
    const { templateType } = this.state;
    
    switch (templateType) {
      case 'Auto':
        return (
          <>
            <div className="templateTitle">
              <p className="noMargin">画板知识库</p>
              <p className="desc-paragraph">
                提供结构化展示，在线完成知识展示。
              </p>
            </div>
            <div className="templateSlotPic">
              {/* 团队插画 */}
              <img src={require('../../../../assets/images/placeHolder.jpg')} />
            </div>
            <div className="useScene">
              <p className="noMargin">使用场景</p>
              <p className="desc-paragraph">使用场景使用场景使用场景使用场景使用场景使用场景</p>
            </div>
          </>
        )
        break;
    
      default:
        break;
    }
  }

  render() {
    return (
      <div className="createNewWrap">
        <div className="title flex noJustify">
          <h3>新建知识库</h3>
          <span>一起收集资料，交流灵感，沉淀经验</span>
        </div>
        <div className="createContent flex">
          <div className="leftSection padding flex">
            {this.renderLeftSection()}
          </div>
          <div className="rightSection padding">
            {this.renderRightSection()}
          </div>
        </div>
      </div>
    )
  }
}

export default connect(state => state.knowledge)(KnowledgeCreateNew);