import React, { useState, useEffect } from 'react';
import { getGroupInfo } from '../../mock/users';
import { Modal, Select, Form, Spin } from 'antd';

const { Option } = Select;

export default function SelectGroupModal(props) {
  const { visible } = props;
  const [groupList, setGroupList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [targetGroup, setTargetGroup] = useState({});
  useEffect(() => {
    const groupList = getGroupInfo();
    setGroupList(groupList);
  }, []);

  const fetchGroup = (keywords) => {
    const groupList = getGroupInfo();
    setLoading(true);

    setTimeout(() => {
      const targetGroups = groupList.filter(group => {
        return group.name.includes(keywords);
      });

      if(targetGroups.length) {
        setGroupList(targetGroups);
      }

      setLoading(false);
    }, 300)
  }

  return (
    <Modal
      visible={visible}
      title="选择团队"
      okText="确认"
      cancelText="取消"
      onCancel={() => props.onCancel}
      onOk={() => props.onOk(targetGroup)}
      >
      <Form>
        <Form.Item label="请选择想要加入的团队">
          <Select 
            placeholder="请选择团队或者搜索团队名称"
            // defaultValue={groupList.length && [groupList[0].id]}
            notFoundContent={loading ? <Spin size="small" /> : null}
            // showSearch={true}
            onSearch={(keywords) => fetchGroup(keywords)}
            onChange={(value) => {
              console.log(value, groupList.filter(group => group.id == value))
              setTargetGroup(groupList.filter(group => group.id == value)[0]);
            }}>
            {groupList.map((group, index) => <Option value={group.id} key={group.id}>
              {group.name}
            </Option>)}
          </Select>
        </Form.Item>
      </Form>
    </Modal>
  )
}